  font-size: 62.5%;
}

body {
  font-family: var(--font-family);
  color: var(--text);
  font-size: 1.6rem;
}

p {
  margin: 1rem 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1rem 0 2rem 0;
}

h2 {
  margin: 2rem 1rem;
}

h3 {
  font-weight: 400;
  margin: 1rem 0.5rem 1rem 0.5rem;
}

ul {
  list-style: none;
}

a {
  color: var(--text);
  text-decoration: none;
}

img {
  max-width: 100%;
}

.title-small {
  letter-spacing: 0.5rem;
  font-size: 1.6rem;
  font-family: var(--font-family-small);
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --font-family-strasua: "strasua";
  --font-family-title: "Cinzel Decorative", cursive;
  --font-family: "Nunito Sans", sans-serif;
  --text: #333;
  --blue: #6eb2bd;
  --orange_01: #df5b13;
  --orange_02: #fd8342;
  --green_01: #7ccbab;
  --green_02: #11a67e;
}

.mb_2 {
  margin-bottom: 2rem !important;
}

.mb_5 {
  margin-bottom: 5rem !important;
}

.mx_1 {
  margin-left: 1rem;
  margin-right: 1rem;
}

.my_1 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#menu,
#menu ul {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

#menu li {
  display: inline-block;
  position: relative;
  border-radius: 8px 8px 0 0;
}

#menu ul li {
  display: inherit;
  border-radius: 0;
}

#menu ul li:hover {
  border-radius: 0;
}

#menu ul li:last-child {
  border-radius: 0 0 8px 8px;
}

#menu ul {
  position: absolute;
  z-index: 1000;
  max-height: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  -moz-transition: 0.8s all 0.3s;
  -webkit-transition: 0.8s all 0.3s;
  transition: 0.8s all 0.3s;
}

#menu li:hover ul {
  max-height: 15em;
}

/* background des liens menus */
#menu li:last-child {
  background: var(--orange_01);
}

/* background des liens sous menus */
#menu li:last-child li {
  background: var(--orange_01);
}

/* background des liens menus et sous menus au survol */
#menu li:last-child:hover,
#menu li:last-child li:hover {
  background: var(--orange_02);
}

/* les a href */
#menu a {
  text-decoration: none;
  display: block;
  padding: 8px 32px;
  color: #fff;
  font-family: arial;
}

#menu ul a {
  padding: 8px 0;
}

#menu li:hover li a {
  color: #fff;
  text-transform: inherit;
}

@font-face {
  font-family: "strasua";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format(opentype);
}
.container {
  display: grid;
  background: #eee;
  grid: "img header" auto "separator separator" auto "logo-container logo-container" auto "general general" auto/25rem auto;
}

header {
  grid-area: header;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 10rem;
  background: white;
}
header h2 {
  margin-left: 2rem;
  margin-top: 3rem;
  letter-spacing: 0.2rem;
  font-family: var(--font-family-title);
  animation-name: text-apparition;
  animation-duration: 2s;
}

div {
  display: flex;
}

div {
  margin: 0 1rem;
}

@keyframes text-apparition {
  from {
    transform: translateY(-2rem);
  }
}
img {
  grid-area: img;
  height: 10rem;
  min-width: 25rem;
}

.separator {
  grid-area: separator;
  margin: 1rem 20rem;
  height: 2px;
  background: var(--orange_01);
}

.content-logo {
  grid-area: logo-container;
  display: flex;
  justify-content: center;
  width: 1fr;
}

.logo-container {
  align-items: center;
  justify-content: center;
  display: flex;
  background: var(--blue);
  min-width: 50rem;
  border-radius: 0.8rem;
  animation-name: apparition;
  animation-duration: 3s;
}
@keyframes apparition {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.logo-container h1 {
  margin-top: 2rem;
  color: white;
  font-family: var(--font-family-strasua);
  font-weight: 200;
  font-size: 3rem;
  letter-spacing: 0.2rem;
}

.general {
  min-height: 100vh;
  grid-area: general;
